// global
.rt-single-icon-box {
  display: flex;
  @include afterparent();
  align-items: start;
  @include rt-trs(0.24s);
  .icon-thumb {
    flex: 0 0 auto;
    //height: 58px
    //margin-right: 30px
    img {
      @include rt-trs(0.24s);
    }
  }
  &.icon-center {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  &.rt-xs-center {
    @include breakpoint(xs) {
      flex-direction: column;
    }
  }
  .iconbox-content {
    width: 100%;
    h4 {
      a {
        color: var(--body-font-color);
      }
    }
  }
  &.plain-list-1 {
    background-color: transparent;
    border-bottom: 1px solid rgb(240, 240, 240);
    &:last-child {
      border: none;
    }
  }
  &.rt-align-center {
    align-items: center;
  }
  &.bg-transparent {
    background-color: transparent;
  }
  &.shdoaw-style {
    box-shadow: 0px 18px 65px 0px rgba(153, 153, 159, 0.2);
    background-color: #fff;
  }
  &:hover {
    &.shdoaw-style {
      box-shadow: 0px 18px 45px 0px rgba(153, 153, 159, 0.2);
      transform: translateY(-10px);
    }
  }
}

.path {
  position: absolute;
  top: -46px;
  left: -54px;
  @include breakpoint(xl) {
    left: -40px;
  }
}

.rt-single-icon-box {
  &.service-box {
    box-shadow: 0px 5px 30px 0px rgba(#828485, 0.12);
    background-color: #fff;
    border-radius: 8px;
    padding-top: 52px;
    padding-left: 30px;
    padding-right: 30px;
    .bg-shape {
      position: absolute;
      right: 0;
      bottom: 0;
    }
    .icon-thumb {
      margin-bottom: 20px;
    }
    .iconbox-content {
      h4 {
        font-size: 24px;
        color: #0b0548;
        margin: 0 0 20px;
        font-weight: 400;
        a {
          color: #0b0548;
        }
      }
      .iconbox-btn {
        padding-top: 20px;
        padding-bottom: 25px;
        a {
          color: var(--custom_color_3);
          img {
            margin-left: 10px;
          }
        }
      }
    }
    &:hover {
      transform: translateY(-5px);
    }
  }
}

// list-item-1
.holo-services {
  margin-top: -110px;
}
.holo-sercises-wraapper {
  max-width: 1200px;
}
.rt-single-icon-box {
  &.list-item-1 {
    background-color: #fff;
    box-shadow: 0px 5px 15px 0px rgba(#000000, 0.07);
    padding-top: 30px;
    padding-bottom: 30px;
    border-radius: 15px;
    padding-left: 30px;
    padding-right: 30px;
    
    .icon-thumb {
      @include hw(68px);
      border-radius:5px;
      text-align: center;
      margin-right: 25px;
      @include afterparent();
      border: 1px dashed #ccc;
      img{
        text-align: center;
        height: 40px;
      }
     
    }
    .iconbox-content {
      h4 {
        font-size: 24px;
        color: var(--heading-font-color);
        margin: 0 0 15px;
        font-weight: 400;
        a {
          color: var(--heading-font-color);
          &:hover{
            color: var(--primary_color);
          }
        }
      }
      p {
        font-size: 16px;
        line-height: 32px;
      }
    }
    .iconbox-btn {
      padding-top: 7px;
      a {
        color: var(--custom_color_3);
        img {
          margin-left: 10px;
        }
      }
    }
    &:hover{
      transform: translateY(-5px);
      .icon-thumb{
        animation: blurInTop 0.4s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
      }
    }
  }
}
.list-parent {
  li.list-child {
    margin-bottom: 35px;
    &:last-child {
      margin-bottom: 0px;
    }
  }
}

// services
.rt-single-icon-box {
  &.service-box2 {
    background-color: #fff;
    box-shadow: 0px 8px 35px 0px rgba(#365857, 0.12);
    border-radius: 5px;
    padding-top: 55px;
    padding-left: 25px;
    padding-right: 25px;
    height: 308px;
    padding-bottom: 70px;
    .icon-thumb {
      position: relative;
      width: 100%;
      img {
        margin: 0 auto;
        display: block;
      }
      span {
        display: block;
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        font-size: 22px;
        font-weight: 700;
        text-transform: uppercase;
        width: 100%;
        text-align: center;
      }
    }
    .iconbox-content {
      padding-top: 50px;
      h4 {
        font-size: 18px;
        color: #0b0548;
        margin: 0 0 13px;
        line-height: 25px;
        font-weight: 400;
      }
    }
    &:hover {
      transform: translateY(-7px);
    }
  }
}
