


.title-font {
    font-family: $heading-font !important;
}
.body-font{
    font-family: $body-font !important;
}
.text-font {
    font-family: $body-font !important;
}



.rt-section-title {
    text-transform: capitalize;
    font-size: 42px;
    margin: 0 0 5px;
    font-weight: 700;
    line-height: 60px;
    display: inline-block;
    @include afterparent();
    @include breakpoint(xs) {
        font-size: 32px;
        line-height: 42px;
    }
    span {
        
        display: block;
        font-size: 18px;
        text-transform: uppercase;
        font-weight: 800;
        
        &.top{
            margin: 0 0 10px;
        }
        &.bottom{
            margin: 10px 0 0 ;
        }
        @include breakpoint(xs) {
            font-size: 14px;
        }
    }
}
.rt-section-title2{
    @extend .rt-section-title;
    font-size: 52px;
    line-height: 70px;
    font-weight: 600;
    @include breakpoint(xs){
        font-size: 32px;
        line-height: 42px;
    }
}
.rt-column-title{
    @extend .rt-section-title;
    font-size: 40px;
    @include breakpoint(xs){
        font-size: 32px;
        line-height: 42px;
    }
}

.text-white {
    .rt-section-title {
        color: #ffffff;
        span {
            color: #fff;
        }
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            color: #fff;
        }
    }
}

.text-white2 {
    .rt-section-title {
        color: #ffffff;
        span {
            color: #496cfe;
        }
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        color: #fff;
    }
    p {
        color: #fff;
    }
}

.rt-black {
    font-weight: 800;
}

.rt-strong {
    font-weight: 700;
}

.rt-medium {
    font-weight: 500;
}

.rt-semiblod {
    font-weight: 600;
}

.rt-light1 {
    font-weight: 100;
}

.rt-light2 {
    font-weight: 200;
}

.rt-light3 {
    font-weight: 300;
}

.underline {
    text-decoration: underline;
}
.positon-parent{
    position: relative;
    z-index: 1;
}

.rt-container {
    width: 100%;
    padding-right: 15px;
    padding-left: 15px;
    margin-right: auto;
    margin-left: auto;
}
@media (min-width: 576px) {
    .rt-container {
        max-width: 540px;
    }
}
@media (min-width: 768px) {
    .rt-container {
        max-width: 720px;
    }
}
@media (min-width: 992px) {
    .rt-container {
        max-width: 960px;
    }
}
@media (min-width: 1200px) {
    .rt-container {
        max-width: 1640px;
    }
}

@for $i from 0 through 10 {
    .rt-mb-#{$i* 10} {
      margin-bottom: $i * 10px;
    }
    .rt-pt-#{$i* 10} {
      padding-top: $i * 10px;
    }
    .rt-spacer-#{$i* 10} {
      height: $i * 10px;
    }
  }
.link{
    font-size: 18px;
}

