.single-rt-banner{
background-repeat: no-repeat;
background-position: center center;
background-size: 100% 100%;
.rt-banner-content{
  h1{
    font-size: 82px;
    line-height: 96px;
    font-weight: 400;
    margin: 0 0 40px;
  }
}
}

.rt-banner-height{
  height: 100vh;
}

.rt-slider-active {
    &:focus{
        outline: none;
    }
  .slick-next,
  .slick-prev {
      position: absolute;
      right: 160px;
      @include breakpoint(xxl){
          right: 0;
      }
      text-align: center;
      color: #000;
      z-index: 66;
      cursor: pointer;
      background-color: #fff;
      box-shadow: 0px 11px 29px 0px rgba(64, 65, 91, 0.11);
      border-radius: 50%;
      @include hw(50px);
      top: 50%;
      transform: translateX(-50%);
      opacity: 1;
      @include rt-trs(0.2s);
      &::before {
          color: #000;
          font-family: "icofont";
          opacity: 1;
          font-size: 25px;
          @include rt-trs(0.2s);
          line-height: 50px;
          @include breakpoint(xs){
              font-size: 16px;
              line-height: 35px;
          }
      }
      @include breakpoint(xs){
          @include hw(35px);
          
      }
      &:hover {
          background-color: var(--primary_color);
          &::before {
              opacity: 1;
              color: #fff;
          }
      }
  }
  .slick-prev {
      left: auto;
      margin-top: 60px;
      &::before {
          content: '\ea9d';
      }
  }
  .slick-next {
      &::before {
          content: '\eaa0';
      }
  }
}


.rt-slider-active {
    .slick-dots{
        bottom: 28px;
        text-align: right;
    }
    .slick-dots li button:before {
        display: none !important;
    }
    .slick-dots {
        margin: 0;
        padding: 0;
        list-style: none;
        li {
            @include afterparent();
            width: 16px;
            display: inline-block;
            height: 16px;
            button {
                border-radius: 50%;
                position: absolute;
                background-color: rgba(#fff, 0.3);
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
            }
        }
    }
    .slick-dots {
        li {
            button {
                overflow: hidden;
                background-color: transparent;
                box-shadow: inset 0 0 0 2px #fff;
                transition: background 0.3s ease;
                &:after {
                    content: '';
                    position: absolute;
                    bottom: 0;
                    height: 0;
                    left: 0;
                    width: 100%;
                    background-color: #fff;
                    box-shadow: 0 0 1px #fff;
                    -webkit-transition: height 0.3s ease;
                    transition: height 0.3s ease;
                    border-radius: 50%;
                }
            }
            &.slick-active {
                button {
                    &::after {
                        height: 100%;
                    }
                }
            }
        }
    }
}